import React, { Component } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faSearch } from "@fortawesome/free-solid-svg-icons";

// CSS modules
import header from "../css/modules/Header.module.css";

export class Header extends Component {
  constructor(props) {
    super();
    this.screenWidth = document.body.clientWidth;
    this.width = props.width;
    this.searcher = React.createRef(); // Get searcher
  }

  toggle = () => {
    // Only for mobile sceen widths
    if (this.screenWidth <= this.width) {
      // Add toggle class fof searcher
      this.searcher.current.classList.toggle(header["searcher_active"]);
      this.searcher.current.childNodes[0].value = "";
    }
  };

  render() {
    return (
      <header className={header.header}>
        <div className={header["header-container"]}>
          <p className={header["header-title"]}>
            <a href="#">Dooboox</a>
          </p>
          <div className={header["header-searcher"]} ref={this.searcher}>
            <input type="text" name="searcher" id={header.search} />
            <div
              className={header["search-icon_box"]}
              onClick={() => this.toggle()}
            >
              <FontAwesomeIcon
                icon={faSearch}
                className={header["search-icon"]}
              />
            </div>
          </div>
        </div>
      </header>
    );
  }
}
