import React, { Component } from "react";

// Utils
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faLink } from "@fortawesome/free-solid-svg-icons";
import { NavLink } from "react-router-dom";

export class LinksBlock extends Component {
  // Use to add block of some links under a title or smth like it.
  constructor(props) {
    super();
    this.state = {};
    this.state.links = props.links;
  }

  link = (props) => {
    // Generate a link
    if (props.refresh) {
      return (
        <a href={props.link} key={props.key} className="tp-link">
          <FontAwesomeIcon icon={faLink} />
          {props.value}
        </a>
      );
    } else {
      return (
        <NavLink to={props.link} key={props.key} className="tp-link">
          <FontAwesomeIcon icon={faLink} />
          {props.value}
        </NavLink>
      );
    }
  };

  linksBlock(props) {
    // Generate a links block
    let links = props.map((item) => {
      return this.link({
        key: item.id,
        value: item.value,
        link: item.link,
        refresh: item.refresh,
      });
    });

    return <div className="tp-links_block">{links}</div>;
  }

  render() {
    return (
      <React.StrictMode>{this.linksBlock(this.state.links)}</React.StrictMode>
    );
  }
}

export class SublinksBlock extends Component {
  // Use to add a list of some subtitle under an image or smth like it
  constructor(props) {
    super();
    this.state = {};
    this.state.sublinks = props.sublinks;
  }

  sublink = (props) => {
    // Generate a sublink
    return (
      <a href={props.link} key={props.key} className="tp-sublink">
        {props.value}
      </a>
    );
  };

  sublinksBlock = (props) => {
    // Generate a sublinks block
    let sublinks = props.map((item) => {
      return this.sublink({
        key: item.id,
        value: item.value,
        link: item.link,
      });
    });

    return <div className="tp-sublinks_block">{sublinks}</div>;
  };

  render() {
    return this.sublinksBlock(this.state.sublinks);
  }
}

export class Text extends Component {
  // Use to add a text (tp-text)
  constructor(props) {
    super();
    this.state = {
      id: props.id,
      value: props.value,
      content: "",
    };
  }

  text = (props) => {
    // Generate a text
    props.id = props.id || false;

    props.value.forEach((t) => {
      props.content += `${t}\n`;
    });

    return (
      <p className="tp-text" id={props.id}>
        {props.content}
        <br />
      </p>
    );
  };

  render() {
    return <React.StrictMode>{this.text(this.state)}</React.StrictMode>;
  }
}
